.header {
  justify-content: space-between;
  align-items: center;
  width: calc(100% - 100px);
  padding: 0 50px;
  display: flex;
}

.header__logo {
  width: 40px;
  height: 40px;
}

.nav__menu {
  list-style: none;
  display: flex;
}

.nav__item:not(:last-child) {
  margin-right: 20px;
  position: relative;
}

.nav__link {
  text-transform: uppercase;
  white-space: nowrap;
  color: #060b35;
  align-items: center;
  height: 60px;
  font-weight: 500;
  text-decoration: none;
  transition: all .3s ease-in-out;
  display: flex;
}

.nav__link:hover, .is-active {
  color: #00acdc;
}

.is-active:after {
  content: "";
  border-bottom: 4px solid #00acdc;
  border-radius: 8px;
  width: 37px;
  position: absolute;
  bottom: 0;
  left: 0;
}

.card {
  box-sizing: border-box;
  border: 1px solid #f3f3f3;
  width: 200px;
  padding: 32px 16px 16px;
}

.card__img {
  width: 160px;
  height: 134px;
  margin: 0 auto 40px;
  display: block;
}

.card__product-description {
  color: #060b35;
  margin-bottom: 4px;
  font-size: 12px;
  font-weight: 500;
  line-height: 18px;
  transition: all .3s ease-in-out;
}

.card__product-description:hover {
  color: #34568b;
}

.card__product-code {
  color: #616070;
  margin-bottom: 16px;
  font-size: 10px;
  line-height: 14px;
}

.card__price {
  justify-content: space-between;
  margin-bottom: 16px;
  display: flex;
}

.card__price-label {
  color: #616070;
  font-size: 12px;
  line-height: 18px;
}

.card__price-value {
  color: #060b35;
  font-size: 16px;
  font-weight: 700;
  line-height: 18px;
}

.card__button {
  color: #fff;
  text-transform: uppercase;
  text-align: center;
  background-color: #00acdc;
  border: none;
  border-radius: 5px;
  width: 100%;
  height: 40px;
  font-size: 14px;
  font-weight: 700;
  line-height: 16px;
  transition: all .3s ease-in-out;
  display: block;
}

.card__button:hover {
  color: #00acdc;
  background-color: #fff;
  border: 1px solid #00acdc;
}

.stars {
  justify-content: space-between;
  align-items: center;
  width: 100%;
  margin-bottom: 24px;
  display: flex;
}

.stars__rate {
  display: flex;
}

.stars__rate-star {
  background-image: url("star.822d561e.svg");
  background-position: center;
  background-repeat: no-repeat;
  width: 16px;
  height: 16px;
  display: flex;
}

.stars__rate-star:not(:last-child) {
  margin-right: 4px;
}

.stars__rate div:nth-child(-n+4) {
  background-image: url("star-active.edda4c43.svg");
}

.stars__reviews {
  color: #060b35;
  font-size: 10px;
  line-height: 16px;
}

.main {
  --grid-col: 1;
  grid-template-columns: repeat(var(--grid-col), 200px);
  box-sizing: border-box;
  justify-content: center;
  gap: 46px 48px;
  padding: 50px 40px;
  display: grid;
}

@media (width >= 488px) {
  .main {
    --grid-col: 2;
  }
}

@media (width >= 768px) {
  .main {
    --grid-col: 3;
  }
}

@media (width >= 1024px) {
  .main {
    --grid-col: 4;
  }
}

body, p, ul, li, a {
  margin: 0;
  padding: 0;
  font-family: Roboto, sans-serif;
  font-size: 12px;
  font-weight: 400;
}
